import {Component, OnInit} from '@angular/core';
import {Product} from '../../models/product';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {
  popular = {
    width: 4,
    height: 4,
    open: false,
  };
  hits = {
    width: 5,
    height: 1,
    open: false,
  };
  items = Array(5).fill(0);
  products = [];

  slideConfig = {
    slidesToShow: 3,
    slidesToScroll: 1,
    dots: true,
    prevArrow: `<svg class="prevBtn" viewBox='0 0 512 512'>
        <use href="../../../assets/icons/home/chevron-back-outline.svg#el"
               x="0"
               y="0" />
</svg>`,
    nextArrow: `<svg class="nextBtn" viewBox='0 0 512 512'>
        <use href="../../../assets/icons/home/chevron-forward-outline.svg#el2"
               x="0"
               y="0" />
               </svg>`,
  };
  constructor() { }


  ngOnInit(): void {
    for (let i = 0; i < 50; i++) {
      this.products.push(new Product({
        category: 'Ключи',
        name: 'Bulletstorm: Full Clip Edition ' + i,
        image: '/src/assets/dev/product.png',
        discount: i % 3 === 2 ? 30 : 0,
        price: 2500,
        isNew: i % 3 === 0,
        isHit: i % 3 === 1
      }));
    }
  }

  range(n: number, startFrom: number): number[] {
    return [...Array(n).keys()].map(i => i + startFrom);
  }

  floor(n: number): number {
    return Math.floor(n);
  }

  openPopular(event): void {
    event.preventDefault();
    this.popular.open = true;
  }

  openHits(event): void {
    event.preventDefault();
    this.hits.open = true;
  }
}
